              // pIdKey: "pId",
              // rootPId: ""
            }
          },
          callback: {
            beforeClick: (treeId, treeNode) => {
              const zTree = $.fn.zTree.getZTreeObj('tree')
              if (treeNode.isParent) {
                zTree.expandNode(treeNode)
                return false
              } else {
                if (treeNode.disabled) {
                  this.eg('')
                  return false
                }
                // node
                if (this.openNode.length > 0) {
                  let same = false
                  this.openNode.map(node => {
                    node.icon = 'static/images/dtree/folder.gif'
                    zTree.updateNode(node)
                    if (node.id === treeNode.id) {
                      same = true
                      return
                    }
                  })
                  this.openNode = []
                  if (same) {
                    treeNode.url = this.host + '#/home/default'
                    this.title = ''
                    zTree.updateNode(treeNode)
                    return true
                  }
                }
                treeNode.icon = 'static/images/dtree/folderopen.gif'
                treeNode.url = treeNode._url
                this.openNode.push(treeNode)
                this.title = treeNode.name
                zTree.updateNode(treeNode)
                return true
              }
            }
          }
        }
      }
