<style>

  .input-group {
    position: relative;
    display: table;
    border-collapse: separate;
  }

  .input-group-btn {
    position: relative;
    font-size: 0;
    white-space: nowrap;
    display: table-cell;
    vertical-align: middle;
    width: 1%;
  }


  html,
  body {
    height: 100%;
    font-size: 14px;
    color: #000;
    padding: 0;
    margin: 0;
    font-family: "Microsoft YaHei";
    /*font-family: "Microsoft YaHei", "Arial Narrow", sans-serif, "";*/
    background-color: #e7e8e8;
  }

  h1 {
    font-size: 24px;
    color: #000000;
  }

  h2 {
    font-size: 20px;
    color: #333333;
  }

  h3 {
    font-size: 20px;
    color: #666666;
  }

  a {
    color: #333;
  }

  a:hover,
  a:visited,
  a:active,
  a:link {
    color: #666;
    text-decoration: none;
  }

  .top a {
    color: #fff;
  }

  .top a:hover,
  .top a:visited,
  .top a:active,
  .top a:link {
    color: #fff;
    text-decoration: none;
  }


  .c-fail,
  .c-red {
    color: #FF6633;
  }

  .c-red a {
    color: #FF6633;
  }


  .modal-header .close {
    margin-top: 5px;
    font-size: 35px;
  }


  /*main*/

  #main-all {
    margin: auto;
    /* width: 1200px; */
    min-width: 1200px;
    max-width: 1920px;
    width: 100%;
    height: 100%;
    overflow: auto;
    /*
    position : relative;
    height: 100%;
    */
  }

  #main-top {
    position: relative;
    height: 105px;
    color: #fff;
    background-image: url(../../static/images/2018guoshui/home/index-header.png);
    /*     background-repeat: no-repeat; */
    background-size: 100% 105px;
    filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='/assets/images/index-header.png');
  }

  #top-img {
    float: left;
    width: 520px;
    height: 83px;
    /* background-image: url(/static/images/2018guoshui/home/zgsw.png); */
    background-image: url(../../static/images/2018guoshui/home/top-title.png);
    background-repeat: no-repeat;
    margin-top: 15px;
    margin-left: 20px;
  }

  #top-title {
    display: none;
    float: left;
    margin-left: 20px;
    margin-top: 28px;
    font-size: 30px;
  }

  #top-menu {
    font-size: 16px;
  }

  /*main*/
  #main_top_menu {
    font-size: 16px;
    position: absolute;
    top: 0;
    right: 0;
    height: 105px;
    margin: 0;
  }

  #mian_top_search {
    float: left;
    margin-top: 36px;
    margin-right: 36px;
    width: 253px;
  }

  #main_top_right {
    float: right;
    width: 320px;
    margin-right: 25px;
    margin-top: 10px;
  }


  #dzswjSs {
    color: #333;
    height: 34px;
    line-height: 34px;
    font-size: 14px;
    margin-right: 0;
    padding-left: 8px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    border: 1px solid transparent;
    width: 100%;
  }


  #main-middle {
    margin-top: 20px;
    overflow: hidden;
    /*
    margin-top: 0;
    position: absolute;
    top: 125px;
    left: 0;
    right: 0;
    bottom: 0;
    */
  }

  #main-left {
    float: left;
    width: 330px;
    /* width: 27%; */
    border: 1px solid #d6d8dc;
    border-radius: 3px;
    font-size: 16px;
    margin-right: 20px;
    background-color: #fff;
    margin-bottom: 1rem;
  }


  .left-panel {
    text-align: left;
  }

  .left-panel .panel-title {
    background-color: #74b1ee;
    height: 40px;
    line-height: 40px;
    color: #fff;
    text-align: left;
    cursor: pointer;
    border-bottom: 1px solid #999;
  }

  .left-panel .panel-title .icon {
    width: 50px;
    display: inline-block;
    padding-right: 0;
    overflow: hidden;
  }

  .left-panel .panel-title i {
    margin-left: 18px;
    margin-right: 16px;
  }

  .left-panel .panel-title .text {
    width: 246px;
    display: inline-block;
    padding-right: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .left-panel .panel-title .text.text-short {
    width: 120px;
  }


  .panel-title .setting {
    display: inline-block;
    float: right;
    font-size: 14px;
    margin-right: 20px;
    cursor: pointer;
    overflow: hidden;
  }

  .left-panel .panel-content {
    background-color: #FFFFFF;
    color: #333333;
    font-size: 14px;
    margin-bottom: 0px;
    /*min-height: 80px;*/
  }

  .left-panel .panel-content li {
    padding: 0px;
    height: 48px;
    line-height: 48px;
    border-bottom: 1px #666 dotted;
    list-style-type: none;
  }

  .left-panel .panel-content a {
    padding-left: 60px;
    display: inline-block;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    background-image: url(../../static/images/2018guoshui/home/li.png);
    background-repeat: no-repeat;
    background-position: 28px 20px;
  }

  .left-panel .panel-content a.actived {
    background-color: #d5e8fc;
  }

  .left-panel .panel-content a:hover {
    background-color: #eaf4ff;
  }


  #main-right {
    margin-left: 350px;
    /* width: 850px; */
    /* width: 70%; */
    /* width: calc(100% - 350px); */
    /* float: right; */
  }

  .right-panel:first-child {
    margin-top: 0px;
  }

  .right-panel {
    margin-top: 20px;
    border: 1px solid #d6d8dc;
    border-radius: 3px;
    min-height: 260px;
    background-color: #fff;
  }


  /*layui */

  .layui-tab-title {
    padding-left: 10px;
    height: 48px;
  }

  .layui-tab {
    margin: 0;
  }

  .layui-tab-brief > .layui-tab-title .layui-this {
    color: #4585eb;
    font-size: 16px;
  }

  .layui-tab-brief > .layui-tab-more li.layui-this:after,
  .layui-tab-brief > .layui-tab-title .layui-this:after {
    border-bottom: 2px solid #4585eb;
  }

  .layui-tab-title .layui-this:after {
    height: 48px;
  }

  .layui-tab-title li {
    color: #666;
    font-size: 16px;
    height: 48px;
    line-height: 48px;
  }

  .tax-ui .ui-item {
    float: left;
    height: 80px;
    width: 80px;
    margin-left: 30px;
    margin-top: 30px;
    /*background-color: greenyellow;*/
    font-size: 14px;
    color: #333;
    text-align: center;
    cursor: pointer;
  }

  .tax-ui .ui-item img {
    height: 52px;
    width: 52px;
  }


  /**/

  .tax-ps ul {
    float: left;
    width: 25%;
  }

  .tax-ps li {
    display: block;
    color: #333;
    font-size: 14px;
    line-height: 48px;
    padding-left: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 80%;
  }

  .tax-ps li i {
    padding-right: 8px;
  }


  .mp.layui-table {
    margin: 0;
  }

  .mp.layui-table thead tr {
    color: #666;
  }

  .mp.layui-table tr {
    color: #333;
    font-size: 14px;
    height: 48px;
    line-height: 48px;
  }

  .mp thead tr.odd,
  .mp tr.odd,
  .mp thead tr.blue,
  .mp tr.blue {
    background-color: #f9fafd;
  }

  .mp tr.even {
  }


  /**/
  .laypage_main * {
    font-size: 14px !important;
  }

  .laypageskin_default .laypage_curr {
    color: #53ACF3 !important;
  }

  .laypage_main a, .laypage_main span {
    border-radius: 3px;
    border-color: #d1d3d8 !important;
    height: 30px !important;
    line-height: 30px !important;
    margin: 0 4px 6px !important;
    box-sizing: border-box;
  }


  .laypage_main .laypage_disable {
    background-color: #F4F6F9 !important;
    color: #999;
    width: 30px;
  }

  .laypage_main input {
    width: 30px !important;
  }

  /**/
  .layui-laydate-content .laydate-ext-weekend {
    color: #FF6633;
  }

  .layui-laydate .layui-day-this {
    background-color: blue !important;
  }

  .layui-laydate-footer {
    border-top-color: transparent;
    text-align: center;
  }

  .laydate-footer-btns {
    right: 60px;
  }


  .laydate-footer-btns span {
    border-color: transparent;
  }

  .layui-laydate .laydate-ext-day-table .layui-this {
    /*background-image: url(/static/images/2018guoshui/home/laydate-select.png);*/
    background-color: transparent !important;
    background-repeat: no-repeat;
    background-position: 2px;
  }

  .layui-laydate .layui-laydate-content .laydate-ext-day-table td:hover {
    /*background-image: url(/static/images/2018guoshui/home/laydate-hover.png);*/
    background-color: transparent !important;
    background-repeat: no-repeat;
    background-position: 2px;
  }


  .layui-laydate-header i:hover, .layui-laydate-header span:hover {
    color: #53acf3 !important;
  }

  /**/
  .layui-laydate-list > li {
    height: 42px !important;
    line-height: 42px !important;
    margin: 0 !important;
  }

  .layui-laydate-content td:hover, .layui-laydate-list li:hover {
    /*background-image: url(/static/images/2018guoshui/home/year-hover.png);*/
    background-color: transparent !important;
    background-repeat: no-repeat;
    background-position-x: 17px;
    background-position-y: -5px;
  }


  .layui-laydate .layui-laydate-list.laydate-year-list .layui-this {
    /*background-image: url(/static/images/2018guoshui/home/year-selected.png);*/
    background-color: transparent !important;
    background-repeat: no-repeat;
    background-position-x: 17px;
    background-position-y: -5px;
  }


  .laydate-month-list li:hover {
    /*background-image: url(/static/images/2018guoshui/home/year-hover.png);*/
    background-color: transparent !important;
    background-repeat: no-repeat;
    background-position-x: 7px;
    background-position-y: -5px;
  }

  .layui-laydate .laydate-month-list .layui-this {
    /*background-image: url(/static/images/2018guoshui/home/year-selected.png);*/
    background-color: transparent !important;
    background-repeat: no-repeat;
    background-position-x: 7px;
    background-position-y: -5px;
  }

  .layui-laydate .layui-laydate-footer {
    border-top: none;
  }

  .layui-laydate .laydate-footer-btns {
    position: unset;
  }

  .layui-laydate .laydate-footer-btns span {
    border: none;
  }

  .layui-laydate-footer span:hover {
    color: #53acf3 !important;
  }

  /**/
  .layui-layer {
    border-radius: 3px !important;
  }

  .layui-layer .layui-layer-title {
    border-radius: 3px 3px 0 0 !important;
  }


  .layui-layer .layui-layer-title {
    height: 40px;
    background-color: #fff;
  }

  .layui-layer .layui-layer-dialog .layui-layer-content {
    padding: 30px;
    min-height: 50px;
  }

  .layui-layer .layui-layer-btn {
    padding-bottom: 20px;
  }

  .layui-layer-shade {
    opacity: 0.4 !important;
  }


  .no_user .main-top-menu {
    display: none !important;
  }


  ::-webkit-input-placeholder { /* WebKit browsers */
    color: #999;
  }

  :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: #999;
  }

  ::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: #999;
  }

  :-ms-input-placeholder { /* Internet Explorer 10+ */
    color: #999;
  }

</style>



